@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
}

body {
  min-height: 100vh;
  /* overflow-y: hidden; */
}

.App {
  width: 100%;
  font-size: 20px;
  background-color: #fff8f0;
}

.header {
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;

  color: #fff8f0;
  background-color: #264027;
}

  .header__title {
    flex: 1 0;
  }

.grid-wrapper {
  width: 100%;
  min-height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.grid {
  padding: 0.5em;

  display: grid;
  place-items: center;
  grid-gap: 0.1em;
  gap: 0.1em;
}

@media only screen and (orientation: portrait) {
  .grid {
    --cell-dimension: calc((100vw - 1em) / var(--smaller-dimension) - 0.1em);

    width: 100%;
    /* height: max-content; */
    grid-auto-flow: row;
    grid-template-columns: repeat(var(--smaller-dimension), 1fr);
    grid-auto-rows: 1fr;
  }
}


@media only screen and (orientation: landscape) {
  .grid {
    --cell-dimension: calc((90vh - 1em) / var(--smaller-dimension) - 0.1em);

    width: -webkit-max-content;

    width: -moz-max-content;

    width: max-content;
    height: 90vh;
    grid-auto-flow: column;
    grid-template-rows: repeat(var(--smaller-dimension), 1fr);
    grid-auto-columns: 1fr;
  }
}
.cell {
  width: var(--cell-dimension);
  height: var(--cell-dimension);

  display: flex;
  justify-content: center;
  align-items: center;

  color: #fff8f0;
  background-color: #23ce6b;

  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
  
.cell:hover {
  background-color: #264027;
}

.cell--revealed, .cell--revealed:hover {
  background-color: hsl(141, 60%, 80%) !important;
}

.cell--revealed > svg {
  opacity: 1 !important;
}


.cell > svg {
  opacity: 0;
  color: #0c1b33;
  font-size: calc(var(--cell-dimension) * 0.6);
}

